/* DEFAULT */
.slide.before {
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
}

.slide,
.slide.after {
  -webkit-transform: translate3d(0, 50%, 0);
          transform: translate3d(0, 50%, 0);
  will-change: transform;
}

.slide.before,
.slide.after {
  opacity: 0;
}

.slide.selected {
  -webkit-transform: translate3d(0, 0, 0) !important;
  transform: translate3d(0, 0, 0) !important;
  z-index: 11;
}
.scroll .slide.selected {
