}

.dialogContainer:not(.bottom) .dialog + .dialog {
  margin-bottom: 0px;
}

.dialogContainer.bottom .dialog + .dialog {
  margin-top: 0px;
}

.dialogContainer.bottom,
.dialog.bottom {
  top: inherit;
  bottom: 0;
}

.dialogContainer.left,
.dialog.left {
  right: inherit;
  left: 0;
}

.dialogContainer > .dialog {
  position: relative;
}

.dialog.hidden {
  display: none;
}

